.popup__title {
  padding: 0;
  margin: 34px 0 42px 36px;
  font-family: "Inter";
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  text-align: center;

  color: #000000;
}

@media screen and (max-width: 700px) {
  .popup__title {
    margin: 25px 0 75px 22px;
    font-size: 18px;
    line-height: 22px;
  }
}
