.profile__name {
  width: 294px;
  margin: 8px 0 0 0;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 114%;
  color: #ffffff;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 700px) {
  .profile__name {
    width: 189px;
    font-size: 27px;
    line-height: 33px;
    text-align: center;
    margin: 26px 0 0 45px;
  }
}
