.profile__edit-button {
  margin: 0 0 5px 18px;
  width: 24px;
  height: 24px;
  background-image: url('../../../images/edit-button.svg');
  background-color: rgba(0, 0, 0, 0);
  background-size: contain;
  padding: 0;
  border: 0;
  cursor: pointer;
}

.profile__edit-button:hover {
  opacity: 0.6;
}

@media screen and (max-width: 700px) {
  .profile__edit-button {
    width: 18px;
    height: 18px;
    margin: 0 0 7px 10px;
  }
}