.profile__description {
  min-width: 336px;
  margin: 0;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 700px) {
  .profile__description {
    min-width: 0;
    width: 282px;
    margin: 8px 0 0 0;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
  }
}
