.elements {
  display: grid;
  gap: 20px 17px;
  grid-template-columns: 282px 282px 282px;
  padding: 0;
  margin: 0;
  justify-content: center;
}

@media screen and (max-width:900px) {
  .elements {
    grid-template-columns: 282px 282px;
  }
}

@media screen and (max-width:600px) {
  .elements {
    grid-template-columns: 282px;
  }
}